<template>
  <div>
    <flatGreyHead></flatGreyHead>
    <div class="header">
      <div class="container">
        <div class="header-top">
          <div class="logo">
            <a href="index.html">
              数据
              <span>番</span>
            </a>
          </div>

          <div class="clearfix"></div>
        </div>
        <div class="top-menu">
          <span class="menu">
            <img src="images/menu.png" alt>
          </span>
          <ul>
            <li>
              <router-link to="/" class="active">首页</router-link>
            </li>
            <li>
              <router-link to="/datas">数据集</router-link>
            </li>
            <li>
              <router-link to="/blogs">论坛</router-link>
            </li>
            <li>
              <router-link to="/solution">Top Solution</router-link>
            </li>
            <li>
              <!-- <a href="blog.html">AI 招聘</a> -->
              <router-link to="/recruit">AI 招聘</router-link>
            </li>
            <li>
              <router-link to="/about">关于我们</router-link>
            </li>

            <div class="clearfix"></div>
          </ul>
        </div>
        <div class="index-banner">
          <div class="wmuSlider example1" style="height: 420px;">
            <div class="wmuSliderWrapper">
              <article style="position: relative; width: 100%; opacity: 1;">
                <div class="banner-wrap">
                  <div class="banner_center">
                    <h1>数据不是一切,但一切都会变成数据</h1>
                    <p>Duis autem vel eum iriure dolor in hendrerit in vulputate velit esse molestie consequat, vel illum dolore eu feugiat nulla facilisis at vero eros et accumsan et iusto odio dignissim qui blandit.</p>
                  </div>
                </div>
              </article>
              <ul class="wmuSliderPagination">
                <li>
                  <a href="#" class>0</a>
                </li>
                <li>
                  <a href="#" class>1</a>
                </li>
                <li>
                  <a href="#" class>2</a>
                </li>
              </ul>
            </div>

            <ul class="wmuSliderPagination">
              <li>
                <a href="#" class>0</a>
              </li>
              <li>
                <a href="#" class>1</a>
              </li>
              <li>
                <a href="#" class="wmuActive">2</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="welcome-top">
      <div class="container">
        <h3>Welcome !</h3>
        <h6>数据番代是一个非盈利性质的数据集网站，本网站以学习交流为目的，分享各种用于机器学习和数据分析的数据集。用户登录后也可以上传并分享自己的数据集。本网站还有kaggle数据竞赛的部分Top解决方案，供各位学习。同时在本网站的招聘交流专栏也有关于数据分析师的招聘信息。</h6>
        <!-- <p>Dolore magnaorem ipsum dolor ser adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore set.sit amet consectetuer Aadipiscing elit, sed diam nonummy. nonummy nibh euismod tincidunt ut laoreet dolore magna.orem ipsum dolor ser adipiscing elit, sed diam nonummy. Dolore magnaorem ipsum dolor ser adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore set.sit amet consectetuer Aadipiscing elit, sed diam nonummy. nonummy nibh euismod tincidunt ut laoreet dolore magna.orem ipsum dolor ser adipiscing elit, sed diam nonummy.</p> -->
      </div>
    </div>

    <div class="events">
      <div class="container">
        <div class="event-grids">
          <div class="col-md-6 upcoming">
            <h3 style="margin-bottom: 40px;">最新数据</h3>
            <div class="upcoming1" style="margin: 20px 40px 20px 20px">
              <div v-for="(item,new_data_i) in list.new_data" :key="new_data_i" @click="dataDetail(item)" style="box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04); margin:20px">

                <el-row style="padding:20px">
                  <!-- 数据集logo -->
                  <el-col :span="10">
                    <el-image :src="fileDownloadPath+'?fileCode='+item.logo" style="width:100px; height:100px">
                      <!-- 默认图片 -->
                      <img src="./../assets/images/数据.svg" slot="error" style="width:100px; height:100px" />
                    </el-image>
                  </el-col>
                  <!-- 数据集title等信息 -->
                  <el-col :offset="1" :span="10">

                    <h2>{{item.title}}</h2>
                    <p>{{item.subTitle}}</p>

                    <el-tag type="danger">{{(item.datasetType ||'') .name}}</el-tag>
                    <!-- <p>{{fsize(item.dataSize)}}</p> -->

                  </el-col>
                </el-row>

              </div>
            </div>
          </div>
          <div class="col-md-6 furniture">
            <h3 style="margin-bottom: 40px;">最新 solution</h3>

            <div class="upcoming1">
              <div v-for="(t,ti) in list.solutions" :key="ti">

                <el-row style="box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04); padding:20px; margin:20px">
                  <!-- 排名 -->
                  <el-col :span="2">
                    <el-row>
                      <el-col :span="22">
                        <span>
                          RANK
                        </span>
                      </el-col>
                      <el-col :span="22">
                        <span>
                          {{t.solutionRank}}
                        </span>
                      </el-col>

                      <el-col :span="22">
                        <span>TOP </span>
                      </el-col>
                      <el-col :span="22">
                        <span>{{t.solutionTop}} </span>
                      </el-col>
                    </el-row>
                  </el-col>

                  <!-- logo -->
                  <el-col :span="10">

                    <el-image :src="t.logo" style="width:100px; height:100px">
                      <img src="./../assets/images/数据集成2.svg" slot="error" style="width:100px; height:100px">
                    </el-image>
                  </el-col>

                  <!-- 基本信息 -->
                  <el-col :span="7">
                    <p>{{t.solutionTitle}}</p>
                    <el-tag>{{t.competition.title}}</el-tag>
                    <p></p>
                  </el-col>
                </el-row>

              </div>
            </div>
          </div>
          <div class="clearfix"></div>
        </div>
      </div>
    </div>

    <div class="team">
      <div class="container">
        <div class="second-head">
          <h3>热门悬赏</h3>
          <div>

            <el-carousel indicator-position="outside">
              <el-carousel-item v-for="(w,wi) in list.want" :key="wi">
                <h3> xxxx </h3>
              </el-carousel-item>
            </el-carousel>

          </div>
        </div>
        <div class="team-top">
          <div class="col-md-3 team-grid" v-for="(c,ci) in list.contris" :key="ci">
            <img :src="c.icon" class="img-responsive" alt="/">
            <h4>{{c.nickName}}</h4>
            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
          </div>
        </div>
      </div>
    </div>
    <!-- 底部 -->
    <Bigfooter></Bigfooter>
    <a href="#" id="toTop">To Top</a>
  </div>
</template>


<script>
import { homeLatest } from "@/api/product.js";
import { getDatasets } from "@/api/datasets.js";
import { fetchMostContributor } from "@/api/admin.js";
import { getBlogs } from "@/api/blog.js";
import { getSolutions } from "@/api/solution.js";
import flatGreyHead from "@/components/header/flatGreyHead";

import Bigfooter from "@/components/footer/Bigfooter";
export default {
  components: {
    Bigfooter,
    flatGreyHead
  },
  created() {
    this.reloadDatas();
    
    this.reloadWant();
    this.reloadTopSolutionHome();
  },
  data() {
    return {
      list: {
        // 最新数据
        new_data: [],
        // 最多贡献者
        contris: [],

        // 悬赏
        want: [],

        // topsolutions
        solutions: []
      }
    };
  },
  methods: {
    // 数据详情
    dataDetail(r) {
      let url = "#/detail?id=" + r.id;
      window.open(url, "_blank");
      // this.$router.push(url,traget="_blank");
    },
    // 刷新首页topsolution
    reloadTopSolutionHome() {
      getSolutions({ pageIndex: 1, pageSize: 4 })
        .then(r => {
          if (r.code == 200) {
            this.list.solutions = r.data.content || [];
          }
        })
        .catch();
    },
    // 刷新悬赏
    reloadWant() {
      getBlogs({ pageIndex: 1, pageSize: 6 })
        .then(r => {
          if (r.code == 200) {
            this.list.want = r.data.content;
            console.log(this);
          }
        })
        .catch();
    },
    // 刷新最多贡献者
    reloadMostContri() {
      let se = this;
      fetchMostContributor(4)
        .then(r => {
          if (r.code == 200) se.list.contris = r.data;
        })
        .catch(e => {});
    },
    //   最新商品
    reloadDatas() {
      let that = this;
      getDatasets({
        pageIndex: 1,
        pageSize: 6
      })
        .then(r => {
          if (r.code == 200) {
            that.list.new_data = r.data.content || [];
          }
        })
        .catch(e => {});
    }
  }
};
</script>



<style scoped>
@import url(./../assets/css/bootstrap.css);
@import url(./../assets/css/style.css);

* {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif !important;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>



